body, .document {
  font: 20px "EB Garamond", serif;
  margin: 0;
  padding: 0;
}

.document ul {
  margin: 0.5rem;
}

#container {
  padding: 1rem 2rem;
}
@media (max-width: 799px) {
  #container {
    padding: 0.5rem 1rem;
  }
}

h1 {
  margin: 0;
  text-align: center;
}
@media (max-width: 799px) {
  h1 {
    font-size: 30px;
    line-height: 1.2;
  }
}

.row {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  border: 3px solid #ccc;
  border-radius: 4px;
  padding: 1rem;
  margin: 1rem 0;
}
@media (max-width: 799px) {
  .row {
    flex-direction: column;
    padding: 0.5rem 0.75rem;
  }
}
.row .editor, .row .document, .row .js-view {
  max-height: 575px;
  overflow-y: auto;
}
.row > .editor {
  flex: 55%;
}
.row > .output {
  flex: 45%;
  position: relative;
}
@media (min-width: 800px) {
  .row > .output {
    border-right: 3px solid #eee;
    padding-right: 2rem;
    margin-right: 1rem;
    width: 45%;
  }
}
@media (max-width: 799px) {
  .row > .output {
    border-bottom: 3px solid #eee;
    padding-bottom: 2rem;
    margin-bottom: 1rem;
  }
}
.row > .output .toggle-output {
  position: absolute;
  bottom: 0;
  right: 1rem;
}

.rest {
  font-size: 22px;
}
@media (min-width: 800px) {
  .rest {
    width: 800px;
    margin: 0 auto;
  }
}

.cm-editor {
  font-size: 85%;
}
.cm-editor .nota-text {
  font-family: "EB Garamond", serif;
  font-size: 108%;
}