@charset "UTF-8";
.cm-completionIcon-react:after {
  content: "⚛";
  font-size: 140%;
  display: block;
  margin-bottom: -1px;
  margin-top: -4px;
  position: relative;
  top: 2px;
}

.translate-error {
  position: absolute;
  z-index: 1000;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 2px;
  color: red;
  background: white;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.2);
  max-width: 100%;
  height: min-content;
  white-space: pre-wrap;
  font-size: 85%;
  overflow-x: auto;
}