@charset "UTF-8";
.textcolor-peach {
  color: #fadfcb;
}

.bgcolor-peach {
  background: #fadfcb;
}

.textcolor-forest-green {
  color: #badcc7;
}

.bgcolor-forest-green {
  background: #badcc7;
}

.textcolor-error {
  color: #be2222;
}

.bgcolor-error {
  background: #be2222;
}

.textcolor-highlight {
  color: #fff9ac;
}

.bgcolor-highlight {
  background: #fff9ac;
}

.document {
  text-align: justify;
  line-height: 1.3em;
  font-family: serif;
  font-size: 18px;
  overflow-wrap: anywhere;
}
.document .footnotes {
  font-size: 14px;
  line-height: 1.2em;
  border-top: 1px solid #ccc;
  padding-top: 10px;
  margin-top: 2rem;
}
.document .footnotes .backlink {
  color: black;
  font-size: 10px;
}
.document .footnotes .footnote-number {
  font-size: 16px;
  margin-right: 10px;
}
.document .footnotes .footnote {
  margin-bottom: 1rem;
  display: flex;
}
.document .footnote-body p {
  text-indent: 0;
}
.document .error {
  color: #be2222;
  padding: 2px;
  border: 1px solid #be2222;
  border-radius: 2px;
}
.document h1,
.document h2,
.document h3,
.document h4,
.document h5,
.document h6 {
  margin: 0;
  text-align: left;
  line-height: 1em;
}
.document h1 {
  font-size: 2em !important;
}
.document a[href],
.document .ref {
  cursor: pointer;
  text-decoration: none;
}
.document a[href]:hover,
.document .ref:hover {
  text-decoration: underline;
}
.document a[href].nolink,
.document .ref.nolink {
  color: inherit;
}
.document a[href].nolink:hover,
.document .ref.nolink:hover {
  text-decoration: none;
  background: rgba(19, 92, 202, 0.1);
}
.document .def-mode .ref.nolink {
  border: 1px solid rgba(19, 92, 202, 0.2);
  padding: 3px;
  display: inline-block;
}
@keyframes yellowflash {
  from {
    background: #ffdc00;
  }
  to {
    background: transparent;
  }
}
.document *:target {
  animation: yellowflash 1s;
  padding: 4px;
  margin: -4px;
  border-radius: 2px;
}
.document p {
  margin: 0;
  hyphens: auto;
}
.document p.noindent {
  text-indent: 0;
}
.document p + p {
  text-indent: 1em;
}
.document .document-title {
  margin-bottom: 2rem;
}
.document .authors .author {
  margin-bottom: 0.25rem;
}
.document .authors .author .author-name {
  font-size: 22px;
}
.document .abstract {
  margin: 1em 0 2rem;
}
.document .section-title {
  margin-bottom: 1rem;
  margin-top: 0;
  font-size: 22px;
}
.document .section-title .section-number {
  margin-right: 20px;
}
h2.document .section-title {
  text-transform: uppercase;
}

.document pre,
.document code {
  font-family: Inconsolata, monospace;
}
.document code {
  border: 1px solid #eee;
  padding: 0 3px 1px;
  border-radius: 4px;
  white-space: nowrap;
}
.document pre {
  margin: 0.5rem 0;
}
.document section {
  margin-top: 2rem;
}
.document .katex {
  font-size: 1.1rem;
}
.document .katex .newline {
  display: inline;
}
.document .katex .newline::before {
  content: "\a";
  white-space: pre;
}
.document .nomargin .katex-display {
  margin: 0;
}
.document .katex-display .base {
  margin-bottom: -0.33em;
}
.document .katex .vlist-t2 > .vlist-r:nth-child(2) > .vlist {
  pointer-events: none;
  height: 0px !important;
}
.document .tex-highlight {
  background: #fff9ac;
}
.document .bib-references {
  text-align: justify;
  font-size: 16px;
  line-height: 1.3em;
}
.document .bib-reference {
  text-indent: -1em;
  padding-left: 1em;
}
.document .row {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
}
.document .row > * {
  margin-right: 2rem;
}
.document .row > *:last-child {
  margin-right: 0;
}
.document .figure.level-1 {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 1rem 0;
  margin: 2rem 0;
}
.document .figure.level-2 {
  margin: 1rem 0;
}
.document .caption {
  font-size: 16px;
  text-align: center;
}
.document sup.footnote-marker {
  vertical-align: top;
  position: relative;
  bottom: 0.25em;
}
.document .tooltip {
  border: 2px solid #aaa;
  background: white;
  padding: 6px 10px;
  border-radius: 4px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  width: max-content;
  z-index: 1000;
}
.document .tooltip .arrow {
  visibility: hidden;
}
.document .tooltip .arrow, .document .tooltip .arrow::before {
  position: absolute;
  width: 8px;
  height: 8px;
  background: inherit;
  border-radius: 2px;
  border: 2px solid transparent;
}
.document .tooltip .arrow::before {
  visibility: visible;
  content: "";
  transform: rotate(45deg);
}
.document .tooltip[data-popper-placement^=top] .arrow {
  bottom: -4px;
}
.document .tooltip[data-popper-placement^=top] .arrow::before {
  border-bottom-color: #aaa;
  border-right-color: #aaa;
}
.document .tooltip[data-popper-placement^=bottom] .arrow {
  top: -8px;
}
.document .tooltip[data-popper-placement^=bottom] .arrow::before {
  border-top-color: #aaa;
  border-left-color: #aaa;
}
.document .tooltip[data-popper-placement^=left] .arrow {
  right: -4px;
}
.document .tooltip[data-popper-placement^=left] .arrow::before {
  border-right-color: #aaa;
  border-top-color: #aaa;
}
.document .tooltip[data-popper-placement^=right] .arrow {
  left: -4px;
}
.document .tooltip[data-popper-placement^=right] .arrow::before {
  border-left-color: #aaa;
  border-bottom-color: #aaa;
}
.document .expandable {
  margin: 1rem 0;
}
.document .expandable .expand {
  cursor: pointer;
  font-family: "Helvetica", sans-serif;
  border: black;
  color: black;
  padding: 2px 4px;
  border-radius: 4px;
}
.document .expandable > .inner {
  transition: all 0.25s ease;
  overflow: hidden;
}
.document .expandable > .inner > div {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0.5rem 1rem;
}
.document .expandable.expanded > .inner {
  margin-top: 10px;
}
.document .smallcaps {
  font-variant: small-caps;
}
.document .togglebox-parent,
.document .togglebox-grandparent {
  display: inline-block;
}
.document .toggle-button {
  font-size: 0.65rem;
  position: relative;
  bottom: 2px;
  margin-left: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 0 2px;
  vertical-align: middle;
  color: #555;
  cursor: pointer;
}
.document .toggle-button.big {
  font-size: 1rem;
}
.document .togglebox {
  display: flex;
  justify-content: center;
}
.document .togglebox > div {
  display: inline-block;
}
.document .inferrule {
  display: inline-block;
  text-align: center;
  margin: 0.5rem 0;
}
.document .inferrule .divider {
  width: 100%;
  height: 1px;
  background: black;
  margin: 6px 0;
}
.document .inferrule .premise {
  margin-right: 2rem;
  display: inline-block;
}
.document .inferrule .premise:last-child {
  margin-right: 0;
}
.document .inferrule .premise-row {
  display: block;
  line-height: 2rem;
}
.document .inferrule .right {
  height: 0;
}
.document .inferrule .right > div {
  position: relative;
}
.document .theorem-body {
  font-style: italic;
}
.document .theorem-body p {
  text-indent: 0;
}
.document .theorem-body ul,
.document .theorem-body ol {
  margin: 0;
}
.document .theorem-body ul li,
.document .theorem-body ol li {
  margin: 0;
}
.document .correspondence {
  line-height: 1.5em;
}
.document .correspondence .link {
  border-bottom: 1px solid black;
  padding-bottom: 1px;
}
.document .correspondence .link.type-1 {
  border-color: #d62728;
}
.document .correspondence.hover-type-1 .link.type-1 {
  border-width: 3px;
}
.document .correspondence .link.type-2 {
  border-color: #2ca02c;
}
.document .correspondence.hover-type-2 .link.type-2 {
  border-width: 3px;
}
.document .correspondence .link.type-3 {
  border-color: #1f77b4;
}
.document .correspondence.hover-type-3 .link.type-3 {
  border-width: 3px;
}
.document .correspondence .link.type-4 {
  border-color: #e377c2;
}
.document .correspondence.hover-type-4 .link.type-4 {
  border-width: 3px;
}
.document .correspondence .link.type-5 {
  border-color: #9467bd;
}
.document .correspondence.hover-type-5 .link.type-5 {
  border-width: 3px;
}
.document .correspondence .link.type-6 {
  border-color: #e17f0e;
}
.document .correspondence.hover-type-6 .link.type-6 {
  border-width: 3px;
}
.document hr {
  border-color: #eee;
  margin: 1rem 2rem;
}
.document ul,
.document ol {
  padding-left: 1.5rem;
}
.document ul > li,
.document ol > li {
  margin-bottom: 0.5rem;
}
.document ul > li:last-child,
.document ol > li:last-child {
  margin-bottom: 0;
}
.document ol.parenkey {
  counter-reset: list;
}
.document ol.parenkey[start="2"] {
  counter-increment: list;
}
.document ol.parenkey > li {
  list-style: none;
}
.document ol.parenkey > li:before {
  content: "(" counter(list, lower-alpha) ") ";
  counter-increment: list;
  margin-left: -1.5rem;
}
.document .full-width-container {
  position: relative;
}
.document .comments {
  position: relative;
  height: 100%;
}
.document .comment {
  border: 1px solid #999;
  background: white;
  padding: 0.5rem;
  border-radius: 4px;
  display: inline-block;
  position: absolute;
  left: 0;
}
.document .comment > p:first-child {
  text-indent: 0;
}
.document q {
  quotes: "“" "”" "‘" "’";
}
.document .portal {
  position: fixed;
  width: 0;
  height: 0;
  z-index: 1000;
}
.document .loader {
  position: relative;
  opacity: 0.2;
}
.document .loader.loaded {
  opacity: 1;
}
.document .loader .loader-overlay {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 1;
  z-index: 1000;
}
.document .loader .loader-overlay .loader-spinner {
  width: 100%;
  height: 100%;
  max-width: 60px;
  max-height: 60px;
  margin: auto;
}
.document .loader .loader-overlay .loader-spinner > div {
  width: inherit;
  height: inherit;
}
@keyframes fade {
  from {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.document .logger {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}
.document .logger .log {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  opacity: 0;
  max-width: 600px;
  background: white;
}